<template>
  <div class="cross-table" ref="crossTable" v-loading="tableProcessingCompleted" element-loading-background="#fff">
    <div class="head-container" ref="headContainer">
      <table ref="theadTable">
        <thead>
          <!-- 表头前5列是固定的 -->
          <tr>
            <th rowspan="2" colspan="3" class="bias-thead">
              <div class="bias-box">
                <span>来源业务</span>
                <span>影响业务</span>
              </div>
            </th>
            <th colspan="2">公司</th>
            <template v-for="(th, index) in crossData.target">
              <template v-if="th.col_level1_last">
                <th :key="`level1-${th.target_co_id}-${index}`" :colspan="th.col_level1_last">{{th.target_co_name}}</th>
              </template>
            </template>
          </tr>
          <tr>
            <th colspan="2">业务类型</th>
            <template v-for="(th, index) in crossData.target">
              <template v-if="th.col_level2_last">
                <th :key="`level2-${th.target_biztype_id}-${index}`" :colspan="th.col_level2_last">{{th.target_biztype_name}}</th>
              </template>
            </template>
          </tr>
          <tr>
            <th>公司</th>
            <th>业务类型</th>
            <th>业务细类</th>
            <th colspan="2" class="bias-thead">
              <div class="bias-box">
                <span>清单类型</span>
                <span>影响业务操作</span>
              </div>
            </th>
            <template v-for="(th, index) in crossData.target">
              <th :key="`level3-${th.target_activity_id}-${index}`">{{th.target_activity_name}}</th>
            </template>
          </tr>
        </thead>
      </table>
    </div>
    <div class="body-container" ref="bodyContainer">
      <vue-scroll :ops="scrollOptions" @handle-scroll="followScroll" ref="vueScroll">
        <table ref="mainTable">
          <tbody class="text-white">
            <tr v-for="(tr, index) in crossData.raw" :key="`tr-${index}`" :data-row="index + 1">
              <!-- 第一列 -->
              <template v-if="tr.row_level1_first">
                <td data-col="1" :key="`level1-${tr.src_co_id}-${index}`" :rowspan="tr.row_level1_first">{{tr.src_co_name}}</td>
              </template>

              <!-- 第二列 -->
              <template v-if="tr.row_level2_first">
                <td data-col="2" :key="`level2-${tr.src_biztype_id}-${index}`" :rowspan="tr.row_level2_first">{{tr.src_biztype_name}}</td>
              </template>

              <!-- 第三列 -->
              <template v-if="tr.row_level3_first">
                <td data-col="3"
                  :key="`level3-${tr.src_wall_prjtype_code || tr.src_bizdetail_code}-${index}`"
                  :rowspan="tr.row_level3_first">
                  {{tr.src_wall_prjtype_name || tr.src_bizdetail_name}}
                </td>
              </template>

              <!-- 第四列 -->
              <td data-col="4" :key="`${tr.list_type_code || 'XZ'}-${index}`">{{tr.list_type_code || 'XZ:：' + (index+1)}}</td>
              
              <!-- 第五列之后的数据渲染 -->
              <template v-for="(th, idx) in crossData.target">
                <td
                  :data-col="idx + 4"
                  :key="`${tr.target_activity_code}-${idx}`"
                  :data-point="`${index+1}#${idx+1}`"
                  :data-axis="`x${idx+1}`"
                  :data-tag="th.target_ids"
                  :class="{ 'after-edit': tr.isEdit, 'activity': tr.target_ids === th.target_ids }"
                  @mouseenter="cellenter($event, idx+1)"
                  @mouseleave="cellleave($event)"
                >
                  <el-tooltip placement="top" :open-delay="200">
                    <div slot="content">
                      影响业务：{{`${tr.target_co_name}-${tr.target_biztype_name}-${tr.target_activity_name}`}}<br/>
                      来源业务：{{`${tr.src_co_name}-${tr.src_biztype_name}-${tr.src_wall_prjtype_name || tr.src_bizdetail_name}-${tr.list_type_code}`}}
                    </div>
                    <div @dblclick="edit(tr, th, `${index+1}#${idx+1}`)">
                      <span>{{ tr.target_ids === th.target_ids ? '√' : 'x' }}</span>
                    </div>
                  </el-tooltip>
                </td>
              </template>
            </tr>
          </tbody>
        </table>
      </vue-scroll>
    </div>
    <div class="fixed-col-container" v-show="hideFixed">
      <div class="fixed-head">
        <table>
          <thead>
            <tr>
              <th rowspan="2" colspan="3" class="bias-thead">
                <div class="bias-box">
                  <span>来源业务</span>
                  <span>影响业务</span>
                </div>
              </th>
              <th colspan="2">公司</th>
            </tr>
            <tr>
              <th colspan="2">业务类型</th>
            </tr>
            <tr>
              <th>公司</th>
              <th>业务类型</th>
              <th>业务细类</th>
              <th colspan="2" class="bias-thead">
                <div class="bias-box">
                  <span>清单类型</span>
                  <span>影响业务操作</span>
                </div>
              </th>
            </tr>
          </thead>
        </table>
      </div>
      <div class="fixed-col" ref="fixedCol">
        <table>
          <tbody>
            <tr v-for="(tr, index) in crossData.raw" :key="`tr-${index}`" :data-row="index + 1">
              <!-- 第一列 -->
              <template v-if="tr.row_level1_first">
                <td data-col="1" :key="`level1-${tr.src_co_id}-${index}`" :rowspan="tr.row_level1_first">{{tr.src_co_name}}</td>
              </template>

              <!-- 第二列 -->
              <template v-if="tr.row_level2_first">
                <td data-col="2" :key="`level2-${tr.src_biztype_id}-${index}`" :rowspan="tr.row_level2_first">{{tr.src_biztype_name}}</td>
              </template>

              <!-- 第三列 -->
              <template v-if="tr.row_level3_first">
                <td data-col="3"
                  :key="`level3-${tr.src_wall_prjtype_code || tr.src_bizdetail_code}-${index}`"
                  :rowspan="tr.row_level3_first">
                  {{tr.src_wall_prjtype_name || tr.src_bizdetail_name}}
                </td>
              </template>

              <!-- 第四列 -->
              <td data-col="4" :key="`${tr.list_type_code || 'XZ'}-${index}`">{{tr.list_type_code || 'XZ:：' + (index+1)}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <!-- 修改弹窗 -->
    <el-dialog
      title="修改提示"
      :visible.sync="dialogVisible"
      :append-to-body="true"
      width="400px">
      <div class="edit-dialog-box">
        <el-switch
          style="display: block"
          v-model="value2"
          active-color="#13ce66"
          inactive-color="#ff4949"
          active-text="取消影响"
          inactive-text="添加影响">
        </el-switch>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false" size="mini">取消</el-button>
        <el-button type="primary" @click="afterEdit()" size="mini">确定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// 滚动条插件文档地址 https://vuescrolljs.yvescoding.org/zh/guide/
import VueScroll from 'vuescroll/dist/vuescroll-native';
// import 'vuescroll/dist/vuescroll.css';

export default {
  name: 'CrossTable',
  props: {
    crossData: {
      type: [Array, Object],
      default() { return {} }
    },
    editMode: {
      type: Boolean,
      default() { return false }
    },
    cancelEdit: {
      type: Boolean,
      defaule() { return false }
    },
    fullScreen: {
      type: Boolean,
      defaule() { return false }
    }
  },
  components: {
    VueScroll
  },
  data() {
    return {
      tableProcessingCompleted: true,
      hideFixed: true,
      dialogVisible: false,
      editItem: {},
      value2: '',
      // 滚动条插件配置项
      scrollOptions: {
        scrollPanel: {
          easing: 'easeInOutQuad'
        },
        vuescroll: {
          wheelScrollDuration: 200,
        },
        // 滚动条外观
        bar: {
          background: '#c1c1c1',
          opacity: .8,
          size: '14px',
          onlyShowBarOnScroll: false,
          keepShow: true
        },
        rail: {
          width: '14px'
        }
      }
    }
  },
  directives: {
    hover: {
      inserted($el) {
        console.log($el)
      }
    }
  },
  // watch: {
  //   fullScreen() {
  //     this.$refs.vueScroll.refresh()
  //     console.log(333)
  //   }
  // },
  created() { },
  mounted() {
    this.$nextTick( () => {
      setTimeout( () => {
        // console.log(this.$refs.vueScroll)
        // this.$refs.vueScroll.refresh();
        let theadHeight = this.$refs.headContainer.offsetHeight;
        // let tableWidth = this.$refs.mainTable.offsetWidth;
        this.$refs.bodyContainer.style.top = `${theadHeight - 2}px`;
        this.$refs.fixedCol.style.top = `${theadHeight - 1}px`;

        // 获取前五列宽度用于固定左边列
        // this.$refs.theadTable
        // console.log([this.$refs.theadTable])
        // console.log(theadHeight, tableWidth)

        this.tableProcessingCompleted = false;
      }, 200)
      
      // console.log(this.crossData)
      // console.log(this.$refs.headContainer)
    })
  },
  methods: {
    // 固定表头，固定第一列内容跟随滚动函数
    followScroll(vertical, horizontal) {
      // vertical, horizontal, nativeEvent
      this.$refs.headContainer.scrollLeft = horizontal.scrollLeft;
      this.$refs.fixedCol.scrollTop = vertical.scrollTop;
    },
    // 交叉表单元格鼠标进入添加 hover 效果函数
    cellenterX($el, isInFixed = false) {
      let target = $el.target.nodeName.toLowerCase();
      if (target === 'td') {
        // 对应列添加 hover 效果
        $el.path[1].classList.add('cross-hover');
        let index = $el.target.dataset.col; // 当前单元格所在列数
        let $colCells = this.$refs.crossTable.querySelectorAll(`[data-col='${index}']`); // 同一列下的单元格
        // 遍历同一列下的单元格添加 hover 效果
        $colCells.forEach( dom => {
          dom.classList.add('cross-hover');
        });
        
        if (isInFixed) {
          // 为左侧固定列对应行数添加 hover 效果
          let $row = this.$refs.bodyContainer.querySelector(`[data-row='${$el.path[1].dataset.row}']`);
          $row.classList.add('cross-hover');
        } else {
          // 当鼠标移到左侧固定列时，对表格内容添加 hover 效果
          let $col = this.$refs.fixedCol.querySelector(`[data-row='${$el.path[1].dataset.row}']`);
          $col.classList.add('cross-hover');
        }
      } else {
        $el.srcElement.classList.add('cross-hover');
      }
    },
    // 交叉表单元格鼠标离开移除 hover 效果函数
    cellleaveX($el) {
      let target = $el.target.nodeName.toLowerCase();
      if (target === 'td') {
        let $colCells = this.$refs.crossTable.querySelectorAll(`.cross-hover`);
        $colCells.forEach( dom => {
          dom.classList.remove('cross-hover');
        });
      } else {
        $el.srcElement.classList.remove('cross-hover');
      }
    },
    cellenter($el, axisX) {
      let target = $el.target.nodeName.toLowerCase();
      if (target === 'td') {
        // 对应列添加 hover 效果
        $el.path[1].classList.add('cross-hover');
        let $cells = this.$refs.crossTable.querySelectorAll(`[data-axis='x${axisX}']`); // 同一列下的单元格
        // 遍历同一列下的单元格添加 hover 效果
        $cells.forEach( dom => {
          dom.classList.add('cross-hover');
        });
      } else {
        $el.srcElement.classList.add('cross-hover');
      }
    },
    cellleave($el) {
      let target = $el.target.nodeName.toLowerCase();
      if (target === 'td') {
        let $cells = this.$refs.crossTable.querySelectorAll(`.cross-hover`);
        $cells.forEach( dom => {
          dom.classList.remove('cross-hover');
        });
      } else {
        $el.srcElement.classList.remove('cross-hover');
      }
    },
    edit(tr, th, point) {
      if (this.editMode) {
        this.dialogVisible = true;
        this.editItem = tr;
        this.point = point;
        // console.log(tr, th)
      } else {
        this.$message.error('未进入编辑模式不允许双击编辑');
      }
    },
    afterEdit() {
      // this.editItem.isEdit = true;
      // console.log(this.$refs.bodyContainer.querySelector(`[data-point="${this.point}"]`))
      this.$refs.bodyContainer.querySelector(`[data-point="${this.point}"]`).classList.add('after-edit');
      this.dialogVisible = false;
    }
  }
}
</script>

<style lang="scss">
// 添加 scoped 会使伪类无效
  $border-color: #ccc;
  $color-blue: #409EFF;
  $color-green: #67C23A;
  $color-orange: #E6A23C;
  $color-red: #F56C6C;
  $color-grey: #909399;

  .cross-table {
    position: absolute;
    top: 62px;
    right: 16px;
    bottom: 16px;
    left: 16px;
    font-size: 14px;
    // border: 1px $border-color solid;
    border-radius: 3px;
    box-shadow: 2px 2px 6px rgba(0, 0, 0, .2);
    overflow: hidden;

    // hover 效果类名
    .cross-hover {
      background-color: rgb(240, 247, 255);
    }
    
    // 受影响时的效果
    .activity {
      background-color: $color-red;
      color: #fff;
    }
    // 编辑后的效果
    .after-edit {
      background-color: $color-orange;
      color: #fff;
    }

    // 斜线框
    .bias-box {
      position: relative;
      width: 100%;
      height: 42px;
      overflow: hidden;

      & > span:first-child {
        position: absolute;
        bottom: 2px;
        left: 2px;
      }

      & > span:last-child {
        position: absolute;
        top: 2px;
        right: 2px;
      }

      &::after {
        // content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: linear-gradient(to top right, transparent, transparent 49%, $border-color, transparent 51%, transparent);
      }
    }

    .bias-thead {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: linear-gradient(to top right, transparent, transparent 49%, $border-color, transparent 51%, transparent);
      }
    }

    table {
      table-layout: fixed;
      margin: 0;
      border: 1px $border-color solid;
      border-collapse: collapse;  
      border-spacing: 0;
      
      th, td {
        // box-sizing: border-box;
        min-width: 100px;
        max-width: 100px;
        padding: 5px 8px;
        border: 1px $border-color solid;
        transition: background-color .1s ease;

        // &:nth-child(1),
        // &:nth-child(2),
        // &:nth-child(3),
        // &:nth-child(4) {
        //   width: 110px;
        // }
      }

      tr {
        transition: background-color .1s ease;
      }
    }

    .head-container {
      position: absolute;
      top: -1px;
      right: 0;
      left: -1px;
      overflow: hidden;
      box-shadow: 0 2px 6px rgba(0, 0, 0, .1);
    }

    .body-container {
      position: absolute;
      left: -1px;
      bottom: 0;
      right: 0;
    }

    .fixed-col-container {
      position: absolute;
      top: -1px;
      left: -1px;
      bottom: 0;
      width: 468px;
      // background-color: #fff;
      background-color: #f3f3f3;
      // overflow: hidden;

      .fixed-head {
        position: absolute;
        top: 0;
        left: 0;
        background-color: #fff;
        overflow: hidden;
      }

      .fixed-col {
        position: absolute;
        // top: 19px; // 动态给值
        left: 0;
        bottom: 0;
        font-weight: bold;
        overflow: hidden;
        box-shadow: 2px 2px 6px rgba(0, 0, 0, .1);
      }
    }

    .__rail-is-vertical {
      right: 9px!important;
    }
    .__rail-is-horizontal {
      bottom: 5px!important;
    }
  }

  .text-white {
    color: #fff;
  }

  .edit-dialog-box {
    height: 80px;
    padding: 10px 90px;
  }
</style>