<script>
import CrossTable from './components/CrossTable.vue'
export default {
  name: 'App',
  components: { CrossTable },
  data() {
    return { 
      corssTableData: {},
      isEditMode: false,
      isCancelEdit: false,
      isFullScreenMode: false,
      searchDialogVisible: false,
      options: [
        {
          value: '1',
          label: '母公司'
        }, {
          value: '2',
          label: '资管子公司'
        }, {
          value: '3',
          label: '信德子公司'
        }, {
          value: '4',
          label: '谦和子公司'
        }, {
          value: '5',
          label: '香港公司'
        }
      ],
      searchFactor: ''
     }
  },
  created() {
    this.$http.get('./data/data.json').then( res => {
      this.computeTableSize(res.data);
    })
  },
  methods: {
    computeTableSize(raw) {
      // 先把数据拆分成两组
      let sourceArray = [];
      let targetArray = [];
      let targetUniqueTags = [];
      let targetUniqueArray = [];

      raw.forEach( (item, index) => {
        let obj = {
          source: [],
          target: []
        };

        // 添加数据
        Object.assign(item, {
          isEdit: false,
          edited_value: 'old',
          original_value: 'old',
          target_ids: ''
        })

        // 拼接影响业务的 id
        let theadTag = `${item.target_co_id}#${item.target_biztype_id}#${item.target_activity_id}`;
        item.target_ids = theadTag;
        
        // source 数据分五级
        // target 数据分三级
        for (let key in item) {
          if (key.indexOf('src_') > -1 || key.indexOf('list_') > -1) {
            obj.source[key] = item[key]
          }
          if (key.indexOf('target_') > -1) {
            obj.target[key] = item[key]
          }
        }

        sourceArray.push(obj.source);
        targetArray.push(obj.target);

        // 表格头数据数组
        if (index === 0 || targetUniqueTags.indexOf(theadTag) < 0) {
          targetUniqueTags.push(theadTag);
          targetUniqueArray.push(obj.target);
        }
      })

      // console.log(sourceArray)
      // console.log(targetUniqueTags)
      // console.log(targetUniqueArray)

      this.colspanThead(targetUniqueArray);
      this.rowspanLeftThead(raw);

      // console.log(targetArray)

      this.corssTableData = {
        raw: raw,
        target: targetUniqueArray
      }

      // console.log(this.corssTableData.raw)
    },
    editModeSwitch(isEdit) {
      if (isEdit) {
        this.isEditMode = true;
        this.$message({
          message: '进入编辑模式',
          type: 'warning'
        });
      } else {
        this.isEditMode = false;
        this.$message({
          message: '已保存修改',
          type: 'success'
        });
      }
    },
    cancelEdit() {
      this.$confirm('修改尚未保存，是否保存？', '确认信息', {
        distinguishCancelAndClose: true,
        confirmButtonText: '保存',
        cancelButtonText: '放弃修改'
      })
      .then(() => {
        this.isEditMode = false;
        this.$message({
          message: '已保存修改',
          type: 'success'
        });
      })
      .catch(() => {
        this.isEditMode = false;
      });
    },
    search() {
      this.searchDialogVisible = false;
    },
    colspanThead(array) {
      let colspanCountLevel1 = 0;
      let colspanCountLevel2 = 0;
      array.forEach( (item, index) => {
        // 计算合并行列
        // colspanCountLevel* 需要等于1;
        // if (index > 0) {
        //   // 表头第一行计算合并列得数量
        //   if (item.target_co_id !== array[index-1].target_co_id) {
        //     array[index-1]['col_level1_last'] = colspanCountLevel1;
        //     colspanCountLevel1 = 1;
        //   } else if (index === (array.length - 1)) {
        //     item['col_level1_last'] = colspanCountLevel1 + 1;
        //   } else {
        //     colspanCountLevel1 += 1;
        //   }
        //   // 表头第二行计算合并列得数量
        //   if (item.target_biztype_id !== array[index-1].target_biztype_id) {
        //     array[index-1]['col_level2_last'] = colspanCountLevel2;
        //     colspanCountLevel2 = 1;
        //   } else if (index === (array.length - 1)) {
        //     item['col_level2_last'] = colspanCountLevel2 + 1;
        //   } else {
        //     colspanCountLevel2 += 1;
        //   }
        // }

        if (index === 0) {
          colspanCountLevel1 += 1;
        } else if (index === array.length - 1){
          array[index-colspanCountLevel1]['col_level1_last'] = colspanCountLevel1 + 1;
        } else {
          if (item.target_co_id !== array[index-1].target_co_id) {
            array[index-colspanCountLevel1]['col_level1_last'] = colspanCountLevel1;
            colspanCountLevel1 = 1;
          } else {
            colspanCountLevel1 += 1;
          }
        }

        if (index === 0) {
          colspanCountLevel2 += 1;
        } else if (index === array.length - 1){
          array[index-colspanCountLevel2]['col_level2_last'] = colspanCountLevel2 + 1;
        } else {
          if (item.target_biztype_id !== array[index-1].target_biztype_id) {
            array[index-colspanCountLevel2]['col_level2_last'] = colspanCountLevel2;
            colspanCountLevel2 = 1;
          } else {
            colspanCountLevel2 += 1;
          }
        }
      })
    },
    rowspanLeftThead(array) {
      let rowspanCountLevel1 = 0;
      let rowspanCountLevel2 = 0;
      let rowspanCountLevel3 = 0;
      let rowspanCountLevel4 = 0;
      // 计算左边固定列合并数量
      // 左边固定列第一列计算合并行数
      array.forEach( (item, index) => {
        // 先判断边界问题
        if (index === 0) {
          rowspanCountLevel1 += 1;
        } else if (index === array.length - 1){
          array[index-rowspanCountLevel1]['row_level1_first'] = rowspanCountLevel1 + 1;
        } else {
          if (item.src_co_id !== array[index-1].src_co_id) {
            array[index-rowspanCountLevel1]['row_level1_first'] = rowspanCountLevel1;
            rowspanCountLevel1 = 1;
          } else {
            rowspanCountLevel1 += 1;
          }
        }
        // 左边固定列第二列计算合并行数
        if (index === 0) {
          rowspanCountLevel2 += 1;
        } else if (index === array.length - 1){
          array[index-rowspanCountLevel2]['row_level2_first'] = rowspanCountLevel2 + 1;
        } else {
          if (item.src_biztype_id !== array[index-1].src_biztype_id) {
            array[index-rowspanCountLevel2]['row_level2_first'] = rowspanCountLevel2;
            rowspanCountLevel2 = 1;
          } else {
            rowspanCountLevel2 += 1;
          }
        }
        // 左边固定列第三列计算合并行数
        if (index === 0) {
          rowspanCountLevel3 += 1;
        } else if (index === array.length - 1){
          array[index-rowspanCountLevel3]['row_level3_first'] = rowspanCountLevel3 + 1;
        } else {
          let tag = item.src_wall_prjtype_id ? 'src_wall_prjtype_id' : 'src_bizdetail_id';
          if (item[tag] !== array[index-1][tag]) {
            array[index-rowspanCountLevel3]['row_level3_first'] = rowspanCountLevel3;
            rowspanCountLevel3 = 1;
          } else {
            rowspanCountLevel3 += 1;
          }
        }
        
        // 左边固定列第四列计算合并行数
        if (index === 0) {
          rowspanCountLevel4 += 1;
        } else if (index === array.length - 1){
          array[index-rowspanCountLevel4]['row_level4_first'] = rowspanCountLevel4 + 1;
        } else {
          
          if (item.list_type_code !== array[index-1].list_type_code) {
            array[index-rowspanCountLevel4]['row_level4_first'] = rowspanCountLevel4;
            rowspanCountLevel4 = 1;
          } else {
            rowspanCountLevel4 += 1;
          }
        }
      })
    }
  }
}
</script>

